import { Component, OnInit , ViewChild ,  ElementRef} from '@angular/core';
import { ModalController , NavParams , ActionSheetController, IonInput  } from '@ionic/angular';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';
import { AuthenticationService } from '../../../services/Authentication.service';
import { TranslateService } from '@ngx-translate/core';
import { MyModalComboPage } from './../../../modals/my-modal-combo/my-modal-combo.page';
import {Plugins,  CameraResultType,  CameraSource} from '@capacitor/core';
import { SafeResourceUrl } from '@angular/platform-browser';
@Component({
  selector: 'app-categoria-create',
  templateUrl: './categoria-create.page.html',
  styleUrls: ['./categoria-create.page.scss'],
})
export class CategoriaCreatePage implements OnInit {
 @ViewChild('fotoa', {static: true}) fotoa: ElementRef;
 @ViewChild('inputId', {  static: false })  inputElement: IonInput;
 fotoimg: SafeResourceUrl;
  Form: FormGroup;
 // @ViewChild('createForm', { static: false }) createForm: FormGroupDirective;
  variables: any = [];
  userinfo: any = [];
  idllave: any ;
  idempresa: any;
  tituloxx = '';
  vgrupo: any = [];
  _ID: any;
  lpermiso: any;
  constructor(
    private modalController: ModalController,
    public translateService: TranslateService    , private authenticationService: AuthenticationService,
    private navParams: NavParams , public actionSheetController: ActionSheetController
  ) { }
  dismissModal() {     this.modalController.dismiss('close');      }
  ngAfterViewInit() {    setTimeout(() => {       this.inputElement.setFocus();  }, 400);}

  ionViewDidEnter(): void {               }
  ngOnInit(): void {
    this.variables = this.navParams.get('variables');
    this.userinfo = this.navParams.get('userinfo');
    this.idllave=  this.navParams.get('idllave');
    this.tituloxx =  this.navParams.get('title');
    this.idempresa =  this.navParams.get('idempresa');
    this.Form = new FormGroup({
       'nombre': new FormControl('', Validators.compose([Validators.required, Validators.pattern('[a-zA-Z0-9 ]*')])),
       'descripcion': new FormControl(''),
       'foto': new FormControl(''),
       'estado': new FormControl('', Validators.required)
    });
    if (this.navParams.get('record') == null) {
       this.translateService.get('NUEVOM').subscribe((value) => {   this.tituloxx = value +' '+ this.tituloxx;this.lpermiso = value; });

    } else {
       this.translateService.get('ACTUALIZAM').subscribe((value) => {this.tituloxx = value + ' '+ this.tituloxx; this.lpermiso = value; });
       this._ID 				=	this.navParams.data.record._id;
       this.Form.controls['nombre'].setValue(this.navParams.data.record.nombre);
       this.Form.controls['descripcion'].setValue(this.navParams.data.record.descripcion);
       this.Form.controls['estado'].setValue(this.navParams.data.record.estado);
       this.authenticationService.convertToDataURLviaCanvas(this.authenticationService.getapifoto() +this.navParams.data.record.foto, 'image/jpeg').
       then( base64 => this.fotoimg = base64);
       this.Form.controls['foto'].setValue(this.navParams.data.record.foto);

     
    }
  }
  

  submitForm0() {

    this.authenticationService.subefoto('',  'Image2s',  this.fotoimg, '' , '' , '', '' , 'create' ,this.idempresa,'', this.idllave).then((result) => {
      let data: any = [];
      data = result;
      this.Form.controls['foto'].setValue(data.url);
  
          const   options: any	     = {
              idempresa: this.idempresa,
              idpapa: this.idllave,
              nombre  : this.Form.controls['nombre'].value,
              descripcion  : this.Form.controls['descripcion'].value,
              foto  : this.Form.controls['foto'].value,
              estado  : this.Form.controls['estado'].value,
              bitacora: { idempresa : this.idempresa , idafiliado: '' ,
              email: this.userinfo[0].email , permiso : this.lpermiso, accion: this.tituloxx}
        };
  
          if (options) {
            this.authenticationService.createReg('', options, 'categorias').then((result) => {
              if (!this.Form.valid) { return; }
              this.modalController.dismiss('ok');
          }, (err) => {
            this.authenticationService.presentAlert(err.error , 'Precaución' , '');
          });
  
          }
  
    }, (err) => {
      this.authenticationService.presentAlert(err.error , 'Precaución' , '');
    });
  }
  
  submitForm1() {
  
  
    this.authenticationService.subefoto('',  'Image2s', this.fotoimg, '' , '' , '', this.Form.controls['foto'].value,'update' ,this.idempresa,this._ID, this.idllave).then((result) => {
      let data: any = [];
      data = result;
      this.Form.controls['foto'].setValue(data.url);
  
          const   options: any	     = {
            idempresa: this.idempresa,
            idpapa: this.idllave,
            nombre  : this.Form.controls['nombre'].value,
            descripcion  : this.Form.controls['descripcion'].value,
            foto  : this.Form.controls['foto'].value,
            estado  : this.Form.controls['estado'].value,
            bitacora: { idempresa : this.idempresa , idafiliado: '' ,
            email: this.userinfo[0].email , permiso : this.lpermiso, accion: this.tituloxx}
        };
  
          if (options) {
            this.authenticationService.createReg(this._ID, options, 'categorias').then((result) => {
              if (!this.Form.valid) { return; }
              this.modalController.dismiss('ok');
          }, (err) => {
            this.authenticationService.presentAlert(err.error , 'Precaución' , '');
          });
  
          }
  
    }, (err) => {
      this.authenticationService.presentAlert(err.error , 'Precaución' , '');
    });
  }
  
  
    submitForm() {
        if (this.navParams.get('record') == null) {
          this.submitForm0();
        } else {
          this.submitForm1();
        }
    }
  

    



  /*************************************************+++++FOTOS */
  async takePicture(op) {
  const image = await Plugins.Camera.getPhoto({
  correctOrientation: true,
            height: 200,
            width: 200,
                quality: 60,
  allowEditing: true,
            resultType: CameraResultType.DataUrl,
            source: CameraSource.Camera
          });
          this.authenticationService.generateFromImage(image.dataUrl, 300, 400, 1, data => {
            switch (op) {
              case 'fotoa': this.fotoimg = data;
              this.Form.controls['foto'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto'].value));
              break;
                default:
            }   
        });
        }
  uploadPWA2(op , controlx) {
         const fileList: FileList = controlx;
         if (fileList && fileList.length > 0) {
                 this.firstFileToBase64(fileList[0]).then((result: string) => {
                 this.authenticationService.generateFromImage(result, 300, 400, 1, data => {
                 switch (op) {
                         case 'fotoa':  this.fotoimg = data;  
                         this.Form.controls['foto'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto'].value));
                         break;
                          default:
                  }
                 });
                 }, (err: any) => {
                 switch (op) {
                 case 'fotoa':  this.fotoimg = null; break;
                 default:
                 }
                 });
  }}
  uploadPWA(op) {
  switch (op) {
  case 'fotoa':  if (this.fotoa == null ) { return;}
  this.uploadPWA2(op , this.fotoa.nativeElement.files); break;
  default:
  }
  }
  private firstFileToBase64(fileImage: File): Promise<{}> {
  return new Promise((resolve, reject) => {
  const fileReader: FileReader = new FileReader();
  if (fileReader && fileImage != null) {
  fileReader.readAsDataURL(fileImage);
  fileReader.onload = () => {
  resolve(fileReader.result);
  };
  fileReader.onerror = (error) => {
  reject(error);
  };
  } else {
  reject(new Error('No file found'));
  }
  });
  }
        async takePicture2(op) {
  if ( op === 'fotoa')  {     this.fotoa.nativeElement.click();  }
        }
        async fotodatos( op ) {
          const actionSheet = await this.actionSheetController.create({
            header: 'Foto',
            cssClass: 'my-custom-class',
            buttons: [{   text: 'Galeria',  role: 'destructive',  icon: 'image-outline',
              handler: () => {
                this.takePicture2(op);
              }
            }, {
              text: 'Camara',
              icon: 'camera-outline',
              handler: () => {
                this.takePicture(op);
              }
            }]
          });
          await actionSheet.present();
        }
      /****************************************COMBO DE DATOS */
      async combodatos( op ,  catalogot, frmt, comp, labelx) {
          const modal = await this.modalController.create({
            component: MyModalComboPage,   swipeToClose: false,     backdropDismiss: false,    animated: true,
            presentingElement: await this.modalController.getTop(),
            componentProps: { variables:  this.variables, userinfo: this.userinfo
              ,  record2 : null , op:  catalogot, frm: frmt, idllave : this.idllave, titlex: 'Buscar ' + labelx}
            // , cssClass: 'modal-fullscreen'
          });
          modal.onWillDismiss().then((data) => {
            let datax: any = [];
            datax = data;
            if (datax.data !==  'close')
            {
              if (datax.data) {this.Form.controls[comp].setValue(datax.data.nombre);
                //  this.Form.controls[comp].id = datax.data._id
                } else {
                  this.Form.controls[comp].setValue('');
                }
            }
          });
          return await modal.present();
        }
}
